pre,
ol,
ul,
form,
figure,
hr {
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ol, ul, li {
  list-style: none;
}

article,
aside,
figure,
footer,
header,
main,
nav,
section {
  display: block;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  background: #FFF;
  margin: 0;
  color: #999;
  font: 300 16px/1.6 "PT Serif", serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
}

.nav-page {
  padding-top: 68px;
}

.view {
  background: #FFF;
  position: absolute;
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  min-height: 100vh;
  width: 100%;
}
@media screen and (min-width: 48.5em) {
  .view {
    min-height: auto;
    display: block;
    position: static;
    transition: none;
  }
}

.button {
  -webkit-tap-highlight-color: transparent;
  -webkit-appearance: none;
  transition: 300ms all;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 2px;
  border-radius: 40px;
  line-height: 1;
  background: #FFF;
  box-shadow: none;
  display: block;
  padding: 13px 20px;
  outline: none;
  margin: 0 auto 15px;
  cursor: pointer;
  border: 1px solid #E11A27;
  width: 220px;
  color: #E11A27;
  font: 700 11px "Roboto", "Helvetica", sans-serif;
}
.button--red {
  background: #E11A27;
  color: #FFFFFF;
}
.button:hover {
  background: #4A4A4A;
  border-color: #4A4A4A;
  color: #FFFFFF;
}
.button--left {
  margin-right: 0;
  margin-left: 0;
}
.button--download {
  border-color: #999;
  color: #999;
  padding: 10px;
  width: 190px;
}
.button--download.hide-download {
  transition: 300ms all 600ms;
  opacity: 0;
  visibility: hidden;
}

.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem 0 1rem;
  position: fixed;
  width: 100vw;
  left: 0;
  top: 0;
  z-index: 100;
  background: #FFF;
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}
@media screen and (min-width: 48.5em) {
  .main-nav {
    display: none;
  }
}
.main-nav h1 {
  margin: 0;
  font-size: 20px;
  transition: 300ms opacity;
  transition-delay: 500ms;
}
.main-nav h1 [aria-hidden=true] {
  opacity: 0;
}
.main-nav__contact, .main-nav__back {
  width: 60px;
  height: 40px;
  position: relative;
  text-indent: 200%;
  overflow: hidden;
  white-space: nowrap;
}
.main-nav__back:before {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0) rotate(45deg);
  border-left: 3px solid #E11A27;
  border-bottom: 3px solid #E11A27;
  height: 15px;
  width: 15px;
  content: "";
}
.main-nav__contact {
  border: none;
  background: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}
.main-nav__contact svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

@media screen and (min-width: 48.5em) {
  #app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  .view {
    flex: 1;
  }
}
[role=banner] {
  display: none;
}
@media screen and (min-width: 48.5em) {
  [role=banner] {
    display: block;
    background: #222;
    padding: 28px 0 23px;
  }
  [role=banner] img {
    width: 200px;
    margin: 0 auto 5px;
  }
}

[role=contentinfo] {
  display: none;
}
@media screen and (min-width: 48.5em) {
  [role=contentinfo] {
    display: block;
    background: #F5F5F5;
    padding: 3.625rem 1rem 5rem;
    text-align: center;
    margin-top: 5.625rem;
  }
  [role=contentinfo] .button {
    background: transparent;
    margin-bottom: 45px;
  }
  [role=contentinfo] .button:hover {
    background: #333;
  }
}

.standard-page {
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
}
.standard-page p {
  max-width: 250px;
  margin: 0 auto 1rem;
}

.download-message,
.update-message {
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 1rem;
  background: #FAFAFA;
  width: 100%;
  border: none;
  box-shadow: 0 1px 7px #CCC;
  -webkit-tap-highlight-color: transparent;
  text-align: left;
  font: 400 12px "Roboto", "Helvetica", sans-serif;
  color: #999;
  height: 55px;
  animation: 1s fadeIn forwards;
  z-index: 400;
  outline: none;
}
.download-message small,
.update-message small {
  float: left;
  color: #999;
  font-size: 1.25em;
  padding: 0.125rem 0;
}
.download-message img,
.update-message img {
  float: right;
  width: 25px;
  height: 25px;
}
.download-message img + img,
.update-message img + img {
  margin-right: 10px;
}
.download-message.fade-out,
.update-message.fade-out {
  animation: 1s fadeOut forwards;
}
@media screen and (min-width: 35em) {
  .download-message,
  .update-message {
    box-shadow: 0 2px 4px #DEDEDE;
    width: 250px;
    bottom: 1rem;
    right: 1rem;
    left: auto;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
.download-message + .update-message,
.update-message + .download-message {
  opacity: 0;
}

h1,
h2,
h3,
h4 {
  margin: 0 0 1rem;
  color: #4A4A4A;
  font: 700 40px "Roboto", Helvetica, sans-serif;
}

h2,
h3 {
  font-size: 20px;
}

h4 {
  font-size: 12px;
}

p {
  font-size: 15px;
  margin: 0 0 1rem;
}

.center {
  text-align: center;
}

a {
  color: inherit;
  text-decoration: none;
}

.home {
  text-align: center;
  max-width: 800px;
  padding: 40px 30px;
  margin: 0 auto;
}
.home img {
  margin: 1.5rem auto 1.5rem;
}
.home > :last-child {
  margin-bottom: 0;
}
.home-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: center;
}
@media screen and (min-width: 48.5em) {
  .home-container {
    min-height: auto;
    display: block;
    padding-top: 25px;
  }
  .home-container h1 {
    margin-bottom: 1.625rem;
  }
  .home-container p {
    font-size: 18px;
  }
}

.splash-overlay {
  background: #222;
  text-align: center;
  color: #FFF;
  position: fixed;
  height: 100vh;
  width: 100vw;
  padding: 2rem;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 13px;
  z-index: 300;
}
.splash-overlay img {
  max-width: 160px;
  margin: 0 auto 1.25rem;
}
.splash-overlay p {
  font-size: 1em;
  color: #999;
  max-width: 175px;
  margin: 0 auto 2.2rem;
}
.splash-overlay p:last-child {
  margin: 5.125rem auto 0;
}
.splash-overlay a {
  font: 700 1em "Roboto", "Helvetica", sans-serif;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s ease;
}

.fade-enter, .fade-leave-active {
  opacity: 0;
}

.contact.fade-enter-active,
.contact.fade-leave-active {
  transition: opacity 0.4s ease;
}

.slide-left-enter,
.slide-right-leave-active {
  transform: translate(100%, 0);
  z-index: 4;
}

.slide-left-leave-active,
.slide-right-enter {
  transform: translate(-100%, 0);
  z-index: 4;
}

@media screen and (min-width: 40em) {
  .slide-left-enter,
  .slide-right-leave-active {
    opacity: 0;
    transform: none;
  }
  .slide-left-leave-active,
  .slide-right-enter {
    opacity: 0;
    transform: none;
  }
}
.videos {
  padding: 0 6px;
  max-width: 950px;
  margin: 0 auto;
}
.videos:after {
  content: "";
  display: table;
  clear: both;
}
.videos h4 {
  padding: 12px 7px 13px;
}
.videos a {
  display: block;
}
.videos a:hover figure:before {
  opacity: 0.7;
}
.videos figure {
  position: relative;
}
.videos figure:before {
  background: linear-gradient(rgba(34, 34, 34, 0), #000);
  position: absolute;
  content: "";
  height: 50%;
  bottom: 0;
  width: 100%;
  right: 0;
  transition: 300ms opacity;
}
.videos figure:after {
  background: url("data:image/svg+xml;charset=utf-8;base64,") center/cover no-repeat;
  transform: translate3d(-50%, -50%, 0);
  position: absolute;
  content: "";
  height: 18.95%;
  width: 18.95%;
  left: 50%;
  top: 50%;
}
.videos small {
  position: absolute;
  bottom: 11px;
  right: 11px;
  font: 400 9px "Roboto", "Helvetica", sans-serif;
  color: #FFF;
  text-align: right;
  z-index: 2;
}
.videos img {
  width: 100%;
}
.videos article {
  position: relative;
  margin-left: 4px;
  width: calc(50% - 2px);
  float: left;
}
.videos article:nth-child(2n+1) {
  margin-left: 0;
  clear: left;
}
@media screen and (min-width: 40em) {
  .videos h4 {
    position: absolute;
    bottom: 17px;
    left: 19px;
    color: #FFF;
    z-index: 3;
    padding: 0;
    font-size: 18px;
    margin: 0;
  }
  .videos figure {
    max-height: 375px;
    overflow: hidden;
  }
  .videos figure:after {
    width: 84px;
    height: 84px;
  }
  .videos figure img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    font-family: "object-fit: cover;";
  }
  .videos article {
    margin-bottom: 30px;
    width: calc(50% - 30px);
  }
  .videos article:nth-child(2n) {
    margin-left: 30px;
  }
  .videos small {
    font-size: 12px;
    bottom: 17px;
    right: 19px;
  }
}

.video {
  padding: 0 6px;
  max-width: 950px;
  margin: 0 auto;
}
.video h1 {
  font-size: 24px;
}
.video__content {
  padding: 16px 7px;
}
.video p {
  max-width: 550px;
}
.video video {
  width: 100%;
  height: auto;
  margin: 0;
  display: block;
}
.video video.playing ~ .video-controls {
  opacity: 0.2;
  transition-delay: 1s;
}
.video video.loading ~ .loader {
  opacity: 1;
  visibility: visible;
}
.video video.pausing ~ .pauser {
  opacity: 1;
  visibility: visible;
}

.video-container {
  position: relative;
  background: #1A1A1A;
}

.video-controls {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 46px;
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5));
  transition: 300ms opacity;
}
.hide-controls .video-controls {
  display: none;
}
.video-controls div {
  max-width: 375px;
  margin: 0 auto;
  position: relative;
  height: 46px;
}
.video-controls:hover {
  opacity: 1 !important;
  transition-delay: 0s !important;
}
.video-controls button {
  background: none;
  border: none;
  outline: none;
  box-shadow: none;
  padding: 0;
  width: 30px;
  height: 30px;
  text-indent: 200%;
  white-space: nowrap;
  overflow: hidden;
  background-repeat: no-repeat;
  background-image: url("/static/images/sprites.png");
  background-size: 19px 164px;
  transition: 200ms background-position;
}
.video-controls .time,
.video-controls button,
.video-controls [type=range] {
  transform: translateY(-50%);
  position: absolute;
  top: 50%;
}
.video-controls #play-button {
  background-position: center 11px;
  width: 41px;
  left: 5px;
}
.video-controls #play-button.playing {
  background-position: center -18px;
}
.video-controls #mute-button {
  background-position: center -115px;
  right: 45px;
}
.video-controls #mute-button.muted {
  background-position: center -143px;
}
.video-controls #fullscreen-button {
  background-position: center -50px;
  right: 9px;
}
.video-controls #fullscreen-button.fullscreen {
  background-position: center -83px;
}
.video-controls #seek {
  width: calc(100% - 180px);
  left: 43px;
}
.video-controls .time {
  right: 85px;
  font: 700 12px "Roboto", "Helvetica", sans-serif;
  color: #FFF;
  margin: 1px 0 0;
}
.video-controls input[type=range] {
  -webkit-appearance: none;
  width: 100%;
  margin: 2.975px 0;
  background-color: transparent;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0, #E11A27), color-stop(0, rgba(255, 255, 255, 0.5)));
}
.video-controls input[type=range]:focus {
  outline: none;
}
.video-controls input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 6.3px;
  cursor: pointer;
  box-shadow: none;
  background: rgba(255, 255, 255, 0);
  border-radius: 0px;
  border: none;
}
.video-controls input[type=range]::-webkit-slider-thumb {
  box-shadow: none;
  border: 1px solid rgba(0, 0, 0, 0);
  height: 12.25px;
  width: 12.25px;
  border-radius: 50px;
  background: #ffffff;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -2.975px;
}
.video-controls input[type=range]::-moz-range-track {
  width: 100%;
  height: 6.3px;
  cursor: pointer;
  box-shadow: none;
  background: rgba(255, 255, 255, 0);
  border-radius: 0px;
  border: none;
}
.video-controls input[type=range]::-moz-range-thumb {
  box-shadow: none;
  border: 1px solid rgba(0, 0, 0, 0);
  height: 12.25px;
  width: 12.25px;
  border-radius: 50px;
  background: #ffffff;
  cursor: pointer;
}
.video-controls input[type=range]::-ms-track {
  width: 100%;
  height: 6.3px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
.video-controls input[type=range]::-ms-fill-lower {
  background: #E11A27;
  border: none;
}
.video-controls input[type=range]::-ms-fill-upper {
  background: rgba(255, 255, 255, 0.5);
  border: none;
  border-radius: 0px;
  box-shadow: none;
}
.video-controls input[type=range]::-ms-thumb {
  box-shadow: none;
  border: 1px solid rgba(0, 0, 0, 0);
  height: 12.25px;
  width: 12.25px;
  border-radius: 50px;
  background: #ffffff;
  cursor: pointer;
  height: 6.3px;
}

.video-container:-moz-full-screen {
  width: 100vw;
  height: 100vh;
}
.video-container:-moz-full-screen video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  font-family: "object-fit: contain; object-position: center;";
}

.video-container:-ms-fullscreen {
  width: 100vw;
  height: 100vh;
}
.video-container:-ms-fullscreen video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  font-family: "object-fit: contain; object-position: center;";
}

.video-container:webkit-full-screen {
  width: 100vw;
  height: 100vh;
}
.video-container:webkit-full-screen video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  font-family: "object-fit: contain; object-position: center;";
}

.video-container:fullscreen {
  width: 100vw;
  height: 100vh;
}
.video-container:fullscreen video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  font-family: "object-fit: contain; object-position: center;";
}

.pauser,
.loader {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 40px;
  height: 40px;
  z-index: 3;
  transition: 300ms visibility, 300ms opacity;
  opacity: 0;
  visibility: visible;
}
.pauser:after,
.loader:after {
  background: url("/static/images/loader.png") center/40px no-repeat;
  animation: 2s spin infinite linear;
  position: absolute;
  content: "";
  height: 40px;
  width: 40px;
  left: 0;
  top: 0;
}

.pauser:after {
  animation: none;
  background: url("/static/images/pauser.png") center/40px no-repeat;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
*::-webkit-media-controls-panel {
  display: none !important;
  -webkit-appearance: none;
}

*::--webkit-media-controls-play-button {
  display: none !important;
  -webkit-appearance: none;
}

*::-webkit-media-controls-start-playback-button {
  display: none !important;
  -webkit-appearance: none;
}

.contact {
  background: rgba(0, 0, 0, 0.71);
  position: fixed;
  z-index: 100;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}
.contact__content {
  background: #FFF;
  width: calc(100% - 10px);
  position: absolute;
  transform: translate3d(-50%, -50%, 0);
  border-radius: 15px;
  max-width: 500px;
  text-align: center;
  padding: 32px 0 0;
  left: 50%;
  top: 50%;
}
.contact__content p {
  line-height: 21px;
  margin: 0 0 8px;
}
.contact__content h3 {
  margin-bottom: 18px;
}
.contact [href^=http] {
  color: #E11A27;
}
.contact__close {
  border-top: 1px solid #F5F5F5;
  padding: 20px 10px;
  display: block;
  font: 700 13px "Roboto", "Helvetica", sans-serif;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin: 1.5rem 0 0;
  cursor: pointer;
  color: #CCC;
}
.contact__close-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}